table {
  width: 100%;
  overflow: auto;
  font-size: 14px;
  margin: 16px 0;
}

table caption {
  padding-top: 8px;
  padding-bottom: 8px;
  color: black;
  text-align: left;
  text-align: center;
  font-weight: bold;
  border: 1px solid #ffba66;
}

table table {
  margin: 4px;
}

table thead th {
  text-align: left;
  padding: 5px 0;
  padding-left: 10px;
}

table tr {
  background-color: #fff;
  border: 2px solid #ffba66;
  vertical-align: top;
}

table tr.settingsTheme ~ tr td {
  border: 1px dashed #ffba66;
}

table tr.settingsTheme ~ tr td:first-child {
  text-align: center;
}

table td {
  padding: 6px 13px;
  border: 1px solid #ffba66;
  vertical-align: top;
}

table th.scope {
  text-align: center;
  padding: 10px 0;
  font-style: italic;
}

table .exclude {
  font-size: 12px;
}

table.events td:first-child {
  width: 200px;
  font-style: italic;
}

table.events td:nth-child(2) {
  width: 100px;
}

table.events td:nth-child(3) {
  width: 100px;
}

table.atrribute td:first-child {
  width: 200px;
  font-style: italic;
}

table.atrribute td:nth-child(2) {
  width: 100px;
}

table.atrribute td:nth-child(3) {
  width: 120px;
}

table.atrribute td:nth-child(4) {
  width: 100px;
}

table.properties td:first-child {
  width: 160px;
  font-style: italic;
}

table.properties td:nth-child(2) {
  width: 150px;
}

table.properties td:nth-child(3) {
  width: 150px;
}

table.multi-table > tbody > tr {
  border: 0px;
}

table.multi-table > tbody > tr > td {
  padding: 0px;
  border: 0px;
}

table.multi-table > tbody > tr > td > table {
  margin: 0px;
}

table.multi-table caption {
  border: 0px;
  border-left: 2px solid #ffba66;
  border-right: 2px solid #ffba66;
}

#api h4, #parameters h4 {
  color: black;
  font-style: italic;
}

.package-manual-cont a {
  display: block;
  width: 12em;
  margin: 0 auto;
  padding: 1em;
  text-align: center;
  background-image: linear-gradient(to bottom, #FF6B09 0, #D55500 100%);
  border-color: #FF8C00 #FF8C00 #cc7000 !important;
  border-bottom: 3px solid #ffba66 !important;
  border-style: solid;
  border-width: 0.1em;
  border-radius: 0.2em;
  text-decoration: none;
  font-weight: bold;
  font-size: 120%;
  text-shadow: black 1px 1px 2px;
  color: white;
}

.package-manual-cont a:hover {
  color: #333333;
}

.browser-support-cont {
  display: block;
  position: relative;
  width: 100%;
  height: auto;
}

.browser-support-wrapper {
  display: block;
  position: relative;
  width: 100%;
  height: 150px;
  text-align: justify;
}

.browser-support-wrapper > div {
  position: relative;
  display: inline-block;
  width: 12%;
  height: 100%;
  padding: 10px;
  text-align: center;
}

.browser-support-wrapper > div:after {
  content: '';
  display: inline-block;
  width: 100%;
}

.browser-support-wrapper > div > div {
  position: relative;
  display: block;
  height: 120px;
  width: 100%;
}

.browser-support-wrapper > div > div + span {
  display: block;
  position: relative;
  width: 100%;
  height: 30px;
  font-size: 12px;
  text-align: center;
}

.browser-support-wrapper > div > div {
  margin: 0 auto;
  position: relative;
  display: block;
}

.browser-support-wrapper > div.browser-ie-cont > div {
  background: url("../../front-end/images/svg/explorer.svg") no-repeat;
  background-size: contain;
  background-position: center;
}

